.AboutPage {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  
}

.about-body {
    background-color: #9FA2B5;
    display: flex;
    flex-direction: row;
    flex: 1;
  }

  .about-left {
    background-color: #394060;
    width: 50%;
    display: flex;
  }

  .about-content {
    font-size: 1.3vw;
    color: white;
    margin: 0;
    font-family: 'JetBrains Mono';
  }

  .about-text{
    font-family:"Abel";
    color: white;
    font-size:3vw;
    padding-left:10%;
    padding-right:10%;
    padding-top:5%;
  }

  .about-right {
    font-family: 'JetBrains Mono'; 
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    overflow:hidden;
  }

  .about-image-container {
    padding: 3%;
  }

  .about-image {
    object-fit:contain;
    max-height: 40vw;
    padding-top: 3%;
    padding-bottom: 3%;
  }


  @media (max-width: 768px) {
    .about-body{
      display: flex;
      flex-direction: column;
      flex: 1;
    }

    .about-right {
      font-family: 'JetBrains Mono'; 
      width: 100%;
      height: 48vh;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      overflow:hidden;
    }
    .about-left {
      background-color: #394060;
      width: 100%;
      display: flex;
      height:48vh;
    }

    .about-content {
      font-size: 1.3vh;
      color: white;
      margin: 0;
      font-family: 'JetBrains Mono';
    }
  
    .about-text{
      font-family:"Abel";
      color: white;
      font-size:3vh;
      padding-left:10%;
      padding-right:10%;
      padding-top:5%;
    }

    .about-image {
      object-fit:contain;
      max-height: 40vh;
      padding-top: 3%;
      padding-bottom: 3%;
    }
  }