.PortfolioPage {
  background-color: #9FA2B5;
  min-height: 100vh;
  display:flex;
  flex-direction:column;
}

.port-body {
  display: flex;
  flex-direction: row;
  flex: 1;
  }

.port-left {
  background-color: #394060;
  display:flex;
  flex-direction: column;
  width: 50%;
  font-family: 'JetBrains Mono';
  flex:1;
  padding-left: 3%;
  padding-top: 40px;
  padding-right: 3%;
}

.port-left-content {
  padding-bottom: 20px;
}

.port-exp {
  font-size:30px;
  color: white;
  padding-bottom:20px;
}

.port-desc {
  display:flex;
  flex-direction:row;
  padding-top: 3%;
  padding-bottom: 30px
}

.desc-text {
  display:flex;
  flex-direction:column; 
}

.desc-title {
  color:white;
  font-size: 20px;
}

.desc-info {
  color:#AAA8C1;
  padding-top: 20px;
}

.desc-scope {
  color:white;
  font-size: 15px;
  padding-top: 20px;
}

.port-right {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content:center;
}

@media (max-width: 768px) {
  .port-body {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  .port-left {
    background-color: #394060;
    display:flex;
    flex-direction: column;
    width:100%;
    min-height:48vh;
    justify-content: center;
    font-family: 'JetBrains Mono';
    width:100vw;
  }

  .port-right {
    width:100%;
    min-height:48vh;
    display: flex;
    flex-direction: row;
    justify-content:center;
    font-family: 'JetBrains Mono';
  }
  
  .port-exp {
    font-size:5vw;
    color: white;
    padding-bottom:20px;
  }

  .desc-title {
    color:white;
    font-size: 3vw;
  }
  
  .desc-info {
    color:#AAA8C1;
    padding-top:20px;
    font-size: 2vw;
  }
  
  .desc-scope {
    color:white;
    font-size: 2vw;
    padding-top: 20px;
  }
}

