
.landing_page {
  background-color: #9FA2B5;
  min-height: 100vh;
  display:flex;
  flex-direction:column;

}
.content {
  display: flex;
  flex-direction: row;
  flex: 1;

}

.landing_page-left {
  background-color: #394060;
  display:flex;
  flex-direction: column;
  width: 50%;
  justify-content: center;
  font-family: 'JetBrains Mono';
  flex:1
}

.landing-text {
  display:flex;
  flex-direction: column;
  flex:1;
  justify-content: center;

}

.social-container {
  position:absolute;
  bottom:10px
}

.Hello {
  font-size: 3vw;
  color: #9FA2B5;
  margin: 0;
  padding-left: 10%;
  padding-right: 5%;

}

.RuXin {
  font-size: 5vw;
  color: white;
  margin: 0;
  padding-left: 10%;
  padding-top: 30px;
  padding-right: 5%;
}

.Intro {
  font-size: 1.5vw;
  color: #EAEBF3;
  margin: 0;
  padding-left: 10%;
  padding-top: 30px;
  padding-right: 10%;
  padding-right: 5%;
}

.landing_page-right {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content:center;

}

.image_container {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  overflow: hidden; /* Ensure the image doesn't overflow */
}

.self {
  object-fit: contain;
  max-height: 85vh;
  padding-left: 10%;
  padding-right: 10%;
}



@media (max-width: 768px) {
  .content {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  .landing_page-right {
    width:100%;
    height:48vh;
    display: flex;
    flex-direction: row;
    justify-content:center;
    font-family: 'JetBrains Mono';
  }

  .landing_page-left {
    background-color: #394060;
    display:flex;
    flex-direction: column;
    width:100%;
    height:48vh;
    justify-content: center;
    font-family: 'JetBrains Mono';
  }

  .self {
    object-fit: contain;
    max-height: 45vh;
  }
  
  .Hello {
    font-size: 4vw;
    color: #9FA2B5;
    margin: 0;
    padding-left: 5%;
    padding-right: 5%;
  
  }
  
  .RuXin {
    font-size: 6vw;
    color: white;
    margin: 0;
    padding-left: 5%;
    padding-top: 30px;
    padding-right: 5%;
  }
  
  .Intro {
    font-size: 2.5vw;
    color: #EAEBF3;
    margin: 0;
    padding-left: 5%;
    padding-top: 30px;
    padding-right: 5%;
  }
}