body {
    font-family: 'Abel', sans-serif;
  }


.Navigator {
    justify-content: space-around;
    padding-top: 10px;
    padding-left: 20px;
    padding-bottom: 5px;
    align-items: center;
    color:white;
}
  
.Navigator a {
    color: #9395A2;
    text-decoration: none;
    font-size: 1.6vw;
    transition: font-size 0.3s ease;
    padding: 0 20px; /* Add padding around each link */
}
  
.Navigator a:hover {
    font-size: 2vw; /* Double the size when hovered */
    color:#394060;
}
  
.Navigator a.active {
    font-size: 2.8vw; /* Double the size when active */
    color:#394060;
}



@media (max-width: 768px) {
    .Navigator {
        display:flex;
        justify-content: space-around;
        padding-top: 10px;
        padding-bottom: 5px;
        align-items: center;
        color:white;
    }

    .Navigator a {
        color: #9395A2;
        text-decoration: none;
        font-size: 2vw;
        transition: font-size 0.3s ease;
        padding: 0 20px; /* Add padding around each link */
    }
      
    .Navigator a:hover {
        font-size: 3vw; /* Double the size when hovered */
        color:#394060;
    }
      
    .Navigator a.active {
        font-size: 3.8vw; /* Double the size when active */
        color:#394060;
    }
}

  